/**
 * @file statusPlaceholder.ts
 * @description  AI 
 *
 * ****
 * - MVU:  <StatusPlaceHolderImpl/>
 * - SAM:  $
 * - :  <VarSystemStatusPlaceholder/>
 *
 * ****
 *  HTML
 *
 * @example
 * // 
 * // : <VarSystemStatusPlaceholder/>
 * // : <div class="status-bar">HP: {{vs_stat_data.hp}}</div>
 *
 * @module events/statusPlaceholder
 */

const MODULE_NAME = "[ST-VarSystemExtension/statusPlaceholder]";

/**  */
export const STATUS_PLACEHOLDER = "<VarSystemStatusPlaceholder/>";

/**
 *  AI 
 *
 * ****
 * -  AI role !== 'user'
 * -  includes
 * -  vs_stat_data 
 * -  refresh: 'affected' 
 *
 * @param messageId -  ID
 * @returns {Promise<boolean>} 
 */
export async function addStatusPlaceholder(
  messageId: number,
): Promise<boolean> {
  try {
    // 
    const messages = window.TavernHelper.getChatMessages(messageId);
    if (!messages || messages.length === 0) {
      console.warn(MODULE_NAME, ` #${messageId} `);
      return false;
    }

    const message = messages[0];

    //  AI 
    if (message.role === "user") {
      console.log(
        MODULE_NAME,
        ` #${messageId} `,
      );
      return false;
    }

    // 
    if (message.message.includes(STATUS_PLACEHOLDER)) {
      console.log(MODULE_NAME, ` #${messageId} `);
      return false;
    }

    // 
    const updatedMessage = message.message + `\n\n${STATUS_PLACEHOLDER}`;

    // 
    await window.TavernHelper.setChatMessages(
      [
        {
          message_id: messageId,
          message: updatedMessage,
        },
      ],
      {
        refresh: "affected", // 
      },
    );

    console.log(MODULE_NAME, ` #${messageId} `);
    return true;
  } catch (error) {
    console.error(MODULE_NAME, `:`, error);
    return false;
  }
}
